/*日期控件 - 带有谓词*/
.yxy_datepicker_footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-left: 6px;
}
.yxy_datepicker_footer span {
  line-height: 16px;
  white-space: nowrap;
  padding: 2px 8px;
  margin: 3px 12px 3px 0;
  border-radius: 10px;
  background: #f7f9fd;
  cursor: pointer;
}
.yxy_datepicker_footer span:hover {
  background: #edf1f7;
}
.yxy_datepicker_footer span.checked {
  background: #FFF7E8FF !important;
}
/*日期控件 - 带有谓词*/
.yxy_datepicker_design {
  position: relative;
}
.yxy_datepicker_design > input {
  padding-right: 30px;
}
.yxy_datepicker_design > input:hover .anticon-star-o {
  display: none;
}
.yxy_datepicker_design > div {
  position: relative;
  margin-top: -29px;
}
.yxy_datepicker_design > div .datepicker-control .u-form-control {
  display: none;
}
.yxy_datepicker_design .uf-close-c {
  font-size: 12px !important;
}
.yxy_datepicker_design .yonicon-close-circle {
  position: absolute;
  right: 6px;
  top: 8px;
}
.yxy_datepicker_design .anticon-star-o {
  display: inline-block;
}
.yxy_datepicker_design .yonicon-close-circle {
  display: none;
}
.yxy_datepicker_pannel {
  width: 260px !important;
}
