.mdf-footer {
  z-index: 999;
}
.mdf-footer .mdf-audit {
  background-color: #f8f8f8;
  padding: 16px;
}
.mdf-footer .mdf-audit.modal-audit {
  margin: 0 -15px;
}
.mdf-footer .mdf-audit.modal-audit .viewCell {
  margin-top: -7px;
}
.mdf-footer .mdf-audit.modal-audit .viewCell > div > .ant-row {
  margin: 0;
}
.mdf-footer .mdf-audit.modal-audit .label-control {
  width: 98px !important;
  color: #999;
  height: 18px;
  padding-left: 16px;
  line-height: 18px;
  -ms-flex: 0 0 72px;
  flex: 0 0 98px;
}
.mdf-footer .mdf-audit.modal-audit .control-width {
  height: 18px;
  line-height: 18px;
}
.mdf-footer .mdf-audit.modal-audit .control-width .control-text {
  border: 0;
  color: #333;
  height: 18px;
  line-height: 18px;
}
