.fileList .ant-row {
  padding-top: 5px;
}
.fileList {
  width: 100px;
  margin-top: 10px;
  text-align: center;
  margin-right: 20px;
  line-height: 20px;
  position: relative;
  margin: 10px 10px 0px 10px;
}
.fileList .u-checkbox {
  position: absolute;
  right: 5px;
  z-index: 3;
  margin-right: 0px !important;
  top: 5px;
}
.fileList .u-checkbox-label {
  padding-left: 16px;
}
.fileList .u-checkbox-label::before {
  border-radius: 50%;
  border-color: #E1E1E1;
  background: #fff;
  width: 18px;
  height: 18px;
}
.fileList .is-checked .u-checkbox-label::before {
  background: #4E97FF;
  box-shadow: none;
  border: 1px solid #ffffff;
}
.fileList .is-checked .u-checkbox-label:after {
  color: #fff;
  line-height: 18px;
  text-align: center;
  font-weight: normal;
  padding-left: 2px;
}
.fileList .is-checked + .fileImg {
  position: relative;
}
.fileList .is-checked + .fileImg::before {
  content: "";
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  background: #daebfe;
  opacity: 0.6;
  border: 1px solid #1a7dff;
  border-radius: 4px;
  z-index: 1;
}
.fileList .fileName {
  width: 100%;
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #333;
  margin-top: 5px;
}
.fileList .fileName .fileGroup {
  line-height: 14px;
}
.fileList .fileImg {
  width: 100px;
  height: 100px;
  background-color: #F8F8F8;
  text-align: center;
  line-height: 100px;
  position: relative;
  overflow: hidden;
  margin-bottom: 5px;
  border-radius: 4px;
}
.fileList .fileImg img {
  max-height: 100%;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  position: absolute;
  max-width: 100%;
}
.fileList .fileImg .icon {
  width: 58px;
  height: 58px;
  vertical-align: middle;
}
.fileList .fileImg .fileBtn {
  height: 30px;
  line-height: 30px;
  display: none;
  position: absolute;
  bottom: 0;
  left: 0;
  color: #fff;
  width: 100%;
}
.fileList .fileImg .fileBtn i {
  font-size: 16px;
  color: #fff;
  vertical-align: middle;
  margin-bottom: 4px;
}
.fileList .fileImg .fileBtn i:hover {
  color: #fff;
}
.fileList .fileImg .fileBtn a {
  display: inline-block;
  width: 50%;
  background: rgba(0, 0, 0, 0.5);
  text-align: center;
  color: #fff;
}
.fileList .fileImg .fileBtn a:hover {
  background: rgba(0, 0, 0, 0.75);
}
.fileList .fileImg .fileBtn a.download {
  float: left;
  line-height: 30px;
}
.fileList .fileImg .fileBtn a.close {
  float: right;
  line-height: 30px;
}
.fileList .fileImg .fileBtn a.onlyClose {
  float: right;
  line-height: 30px;
  width: 100%;
}
.fileList .fileImg .fileBtn-group a {
  width: 33.33%;
}
.fileList .fileImg .fileBtn-sort a.sort-left {
  width: 33%;
}
.fileList .fileImg .fileBtn-sort a.sort-right {
  width: 33%;
}
.fileList .fileImg .fileBtn-sort a.close {
  float: right;
  line-height: 30px;
  width: 33%;
}
.fileList .fileImg .showCover {
  display: block;
}
.fileList .fileImg .showCover a {
  width: 100%;
}
.fileList .fileImg:hover .fileBtn {
  display: block;
}
.fileList .item-checked::before {
  content: "";
  background: rgba(87, 153, 240, 0.2);
  border: 1px solid #b5d1f7;
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0px;
  z-index: 2;
}
.fileList .fileSize {
  display: block;
  color: #999;
}
.imageList .Progress {
  z-index: 1001;
}
.imageList .imageName {
  width: 100%;
}
.imageList .imageDiv {
  display: none;
}
.imageList:hover .imageDiv {
  display: block;
  position: absolute;
  top: 70%;
  width: 100%;
  height: 30%;
  background-color: white;
  opacity: 0.3;
  margin-right: 10px;
}
.imageList:hover .arrowleft {
  position: absolute;
  background: url(/static/styles/default/images/components/upload/arrow-left.png);
  filter: "progid:DXImageTransform.Microsoft.AlphaImageLoader(sizingMethod='scale')";
  background-size: 100% 100%;
  top: 30%;
  width: 15%;
  height: 40%;
  left: 13%;
}
.imageList:hover .arrowright {
  position: absolute;
  background: url(/static/styles/default/images/components/upload/arrow-right.png);
  filter: "progid:DXImageTransform.Microsoft.AlphaImageLoader(sizingMethod='scale')";
  background-size: 100% 100%;
  top: 30%;
  width: 15%;
  height: 40%;
  left: 41%;
}
.imageList:hover .delete {
  position: absolute;
  background: url(/static/styles/default/images/components/upload/delete.png);
  filter: "progid:DXImageTransform.Microsoft.AlphaImageLoader(sizingMethod='scale')";
  background-size: 100% 100%;
  top: 30%;
  width: 10%;
  height: 40%;
  left: 70%;
}
.upload-tabs-wrapper .upload-tabs {
  display: -ms-flexbox;
  display: flex;
}
.upload-tabs-wrapper .upload-tabs .u-tabs-bar {
  width: 322px;
  border: none;
  border-right: #eee solid 1px;
  background: #fff;
  height: 370px;
  overflow-y: auto;
  overflow-x: hidden;
  padding-bottom: 10px;
}
.upload-tabs-wrapper .upload-tabs .u-tabs-bar .u-tabs-tab-prev,
.upload-tabs-wrapper .upload-tabs .u-tabs-bar .u-tabs-tab-next {
  display: none;
}
.upload-tabs-wrapper .upload-tabs .u-tabs-bar .u-tabs-nav-scroll {
  height: 100%;
  position: relative;
  background: #fff;
  border: none;
}
.upload-tabs-wrapper .upload-tabs .u-tabs-bar .u-tabs-nav-scroll > .u-tabs-nav {
  max-height: 360px;
  overflow-y: auto;
  width: 219px;
  position: absolute;
  left: 103px;
  height: auto;
}
.upload-tabs-wrapper .upload-tabs .u-tabs-bar .u-tabs-nav-container {
  height: 100%;
}
.upload-tabs-wrapper .upload-tabs .u-tabs-bar .u-tabs-nav-wrap .u-tabs-ink-bar {
  display: none !important;
}
.upload-tabs-wrapper .upload-tabs .u-tabs-bar .u-tabs-nav-wrap .u-tabs-tab {
  width: 200px;
  border-radius: 4px;
  margin-bottom: 0px;
  text-align: left;
  padding: 7px 0px 7px 12px !important;
  border-bottom: none;
  background: #fff;
  font-size: 12px;
  line-height: 22px;
  color: #333;
  white-space: normal;
}
.upload-tabs-wrapper .upload-tabs .u-tabs-bar .u-tabs-nav-wrap .u-tabs-tab .upload-tabname {
  display: block;
  position: relative;
  padding-right: 45px;
}
.upload-tabs-wrapper .upload-tabs .u-tabs-bar .u-tabs-nav-wrap .u-tabs-tab .upload-tabname .upload-filecount {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: #dfdfdf;
  right: 10px;
  font-weight: normal;
}
.upload-tabs-wrapper .upload-tabs .u-tabs-bar .u-tabs-nav-wrap .u-tabs-tab:hover {
  background: #EEF3FA;
}
.upload-tabs-wrapper .upload-tabs .u-tabs-bar .u-tabs-nav-wrap .u-tabs-tab-active {
  border: none;
  background: #FFF7E7;
  font-weight: bold;
  line-height: 22px;
  margin-top: 0;
}
.upload-tabs-wrapper .upload-tabs .u-tabs-content {
  width: calc(100% - 350px) !important;
}
.upload-tabs-wrapper .upload-tabs .u-tabs-content .u-tabs-tabpane {
  margin-top: 0px !important;
}
.upload-tabs-wrapper .upload-tabs .u-tabs-content .u-tabs-tabpane .upload-content {
  margin-top: 0px;
}
.upload-tabs-wrapper .upload-tabs .u-tabs-content .u-tabs-tabpane .fileupload {
  overflow: hidden;
  padding-top: 1px;
}
.upload-tabs-wrapper .upload-tabs .u-tabs-content .u-tabs-tabpane .fileupload .u-button,
.upload-tabs-wrapper .upload-tabs .u-tabs-content .u-tabs-tabpane .fileupload > span,
.upload-tabs-wrapper .upload-tabs .u-tabs-content .u-tabs-tabpane .fileupload > span .u-upload,
.upload-tabs-wrapper .upload-tabs .u-tabs-content .u-tabs-tabpane .fileupload .btn-gray {
  float: left;
}
.upload-tabs-wrapper .upload-tabs .u-tabs-content .u-tabs-tabpane .fileupload .fileupload-txt {
  width: 100%;
}
.viewCell.width-percent-100 .appendix-group-select .u-select,
.viewCell.width-percent-100 .appendix-group-select .control-text.invoice-remark {
  width: 260px !important;
}
