/*表单模版设计*/
.uretail-billdesign-body {
  margin-left: 24px;
  overflow-y: auto;
}
.uretail-billdesign-body .uretail-print-list h3 {
  font-size: 16px;
  color: #333;
  line-height: 48px;
  margin-top: 13px;
}
.uretail-billdesign-body .billdesign-card {
  border: 1px solid #dbe0e5;
  padding: 14px;
  margin: 0 15px 15px 0;
  height: 108px;
  position: relative;
  border-radius: 2px;
  text-align: center;
}
.uretail-billdesign-body .billdesign-card .billdesign-card-title {
  font-size: 16px;
  color: #333;
  line-height: 36px;
  margin: 6px auto 20px;
  height: 36px;
  overflow: hidden;
  max-width: 200px;
}
.uretail-billdesign-body .billdesign-card .billdesign-card-operation {
  color: #e7e7e7;
}
.uretail-billdesign-body .billdesign-card .billdesign-card-operation a {
  margin: 0 10px;
  color: #666;
}
.uretail-billdesign-body .billdesign-card:hover {
  border-color: #9aa7bb;
  box-shadow: 1px 0 10px rgba(0, 0, 0, 0.1);
}
.uretail-billdesign-body .billdesign-card:hover .print-card-btn-setDefault {
  display: block;
}
.bill-design-modal .ant-modal-body {
  padding: 0px;
  min-height: auto;
}
.bill-design-modal .ant-modal-body .billing-design-hide {
  display: none;
}
.bill-design-modal .ant-tabs-extra-content {
  margin-right: 10px;
}
.bill-design-modal .ant-tabs-extra-content button {
  margin-left: 10px;
}
.bill-design-modal .bill-design-tabs .tabs-control {
  width: 800px;
  float: left;
  margin-left: 20px;
}
.bill-design-modal .bill-design-tabs .tabs-control div {
  float: left;
  margin-right: 30px;
  font-size: 14px;
  height: 30px;
}
.bill-design-modal .bill-design-tabs .tabs-control div:hover {
  cursor: pointer;
}
.bill-design-modal .bill-design-tabs .tabs-control div.bill-design-tab-active {
  color: #ee2223;
  position: relative;
}
.bill-design-modal .bill-design-tabs .tabs-control div.bill-design-tab-active:after {
  content: "";
  position: absolute;
  background-color: #505766;
  width: 0 !important;
  height: 0;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-bottom: 4px solid #505766;
  background: none;
  left: 50%;
  bottom: 0;
  margin-left: -3px;
}
.bill-design-modal .bill-design-tabs .tabs-button {
  float: right;
  margin-right: 20px;
  margin-top: 10px!important;
  position: absolute;
  right: 0;
}
.bill-design-modal .bill-design-tabs .tabs-button button {
  margin-left: 10px;
}
.bill-design-modal .bill-design-tabs .tabs-button button svg {
  width: 15px;
  height: 15px;
  margin-right: 5px;
}
.bill-design-modal .bill-design-tabs .noTabs {
  margin-bottom: 10px;
}
/*表单模版设计 弹窗*/
.bill-design-modal .bill-design-tabs .ant-tabs-ink-bar {
  display: none!important;
}
.bill-design-modal .bill-design-tabs .ant-tabs-nav .ant-tabs-tab {
  padding: 8px 0px 15px 10px;
  margin-right: 15px;
}
.bill-design-modal .bill-design-tabs .ant-tabs-bar {
  margin-top: 40px!important;
  margin-bottom: 0px;
}
.bill-design-modal .bill-design-tabs .ant-tabs-bar .ant-tabs-tab-next-icon,
.bill-design-modal .bill-design-tabs .ant-tabs-bar .ant-tabs-tab-prev-icon {
  margin-top: -2px;
}
.bill-design-modal .bill-design-tabs .ant-tabs-bar .ant-tabs-tab-active::before {
  content: "";
  position: absolute;
  background-color: #505766;
  width: 0!important;
  height: 0;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-bottom: 4px solid #505766;
  background: none;
  left: 50%;
  bottom: 2px;
  margin-left: -3px;
}
.bill-design-modal .readOnlyTable .fixedDataTableRowLayout_rowWrapper .textCol .edit-input-number {
  margin-top: 0px;
}
.ant-modal.bill-design-modal .ant-modal-header {
  border-bottom: 1px solid #e9e9e9 !important;
  padding: 11px 16px !important;
}
