.advance-scheme-modal.u-modal {
  z-index: 1699!important;
}
.advance-scheme-modal.u-modal .u-modal-body {
  padding: 0;
  margin: 0;
  margin-top: 10px;
}
.advance-scheme-modal.u-modal .u-modal-body .listheadRow {
  margin: 0;
  padding-top: 0;
}
.advance-scheme-modal.u-modal .u-modal-body .listheadRow > .ant-row {
  margin-right: 20px;
  padding-left: 58px;
}
.advance-scheme-modal.u-modal .u-modal-body .scheme-list {
  margin-bottom: 10px;
}
.advance-scheme-modal.u-modal .u-modal-content .u-modal-header {
  padding: 2px 15px;
}
.advance-scheme-modal.u-modal .u-modal-content .u-modal-header .u-close {
  position: fixed;
  top: 8px;
  right: 17px;
  cursor: pointer;
}
.advance-scheme-modal.u-modal .u-modal-content .u-modal-header .u-close .uf-close {
