  margin: -10px -20px 0;
}
.advance-tabs .u-tabs-nav-wrap {
  padding-right: 60px;
}
.advance-tabs .advance-conditions {
  margin: 0 20px;
}
.advance-tabs .advance-tabpanes {
  margin-bottom: 10px;
  padding: 5px;
}
.advance-button.u-button {
  position: absolute;
  color: #588ce9;
  top: -8px;
  right: 0;
}
.advance-button.u-button:hover {
  color: #505766;
}
.viewCell .ant-form-explain {
