.Formula-Designer {
  height: 586px;
  background: #fff;
  box-shadow: 0 0 14px 6px #666666;
  border-radius: 6px;
}
.Formula-Designer .ant-input-number,
.Formula-Designer .u-input-number {
  height: 30px;
}
.Formula-Designer .ant-input-number-input,
.Formula-Designer .u-input-number-input {
  height: 28px;
}
.Formula-Designer .ant-select-selection--single,
.Formula-Designer .u-select-selection--single {
  height: 28px !important;
}
/*头部*/
.Formula-Designer .ant-modal-content .ant-modal-header,
.Formula-Designer .u-modal-content .u-modal-header {
  padding: 11px 20px;
}
/*内容*/
.Formula-Designer .ant-modal-content .ant-modal-body,
.Formula-Designer .u-modal-content .u-modal-body {
  height: 494px;
  padding: 0px 0px 0px 20px;
  max-height: 494px;
}
.u-modal .Formula-Designer .u-modal-title {
  margin: 0;
  font-size: 14px;
  line-height: 21px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.85);
}
.Formula-Designer .ant-modal-content .ant-modal-body .LeftContent,
.Formula-Designer .u-modal-content .u-modal-body .LeftContent {
  width: 232px;
  float: left;
  height: 494px;
  overflow-y: auto;
  border-right: #E4E4E4 solid 1px;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent,
.Formula-Designer .u-modal-content .u-modal-body .RightContent {
  width: 514px;
  float: left;
  margin-left: 34px;
  padding-top: 20px;
  height: 494px;
  overflow-y: auto;
}
.Formula-Designer .u-modal-content .u-modal-body .RightContent {
  width: 505px;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .add_defined .refer-div.has-feedback .ant-row,
.Formula-Designer .u-modal-content .u-modal-body .RightContent .add_defined .refer-div.has-feedback .ant-row {
  margin-bottom: 0;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .ant-row,
.Formula-Designer .u-modal-content .u-modal-body .RightContent .ant-row {
  margin-bottom: 15px;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .ant-row .label-control,
.Formula-Designer .u-modal-content .u-modal-body .RightContent .ant-row .label-control {
  width: 68px !important;
  color: #2A2A2A;
  text-align: right;
  line-height: 28px;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .ant-row .input-control,
.Formula-Designer .u-modal-content .u-modal-body .RightContent .ant-row .input-control {
  margin-left: 15px;
  width: 350px !important;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .ant-row .input-control .ant-select,
.Formula-Designer .u-modal-content .u-modal-body .RightContent .ant-row .input-control .u-select {
  float: left;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .ant-row .input-control .ant-tag,
.Formula-Designer .u-modal-content .u-modal-body .RightContent .ant-row .input-control .u-tag {
  min-width: 50px;
  height: 30px;
  background: #fff;
  line-height: 26px;
  text-align: center;
  margin-bottom: 10px;
  margin-left: 0px;
  margin-right: 10px;
  border: 1px solid #D9D9D9;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .ant-row .input-control .ant-tag .ant-tag-text,
.Formula-Designer .u-modal-content .u-modal-body .RightContent .ant-row .input-control .u-tag .u-tag-text {
  color: #666;
  font-size: 15px;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .ant-row .input-control .ant-tag:hover,
.Formula-Designer .u-modal-content .u-modal-body .RightContent .ant-row .input-control .u-tag:hover {
  border: 1px solid #505766;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .ant-row:nth-of-type(1),
.Formula-Designer .u-modal-content .u-modal-body .RightContent .ant-row:nth-of-type(1) {
  margin-bottom: 20px;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .ant-row:nth-of-type(2),
.Formula-Designer .u-modal-content .u-modal-body .RightContent .ant-row:nth-of-type(2) {
  margin-bottom: 15px;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .ant-row:nth-of-type(2) .input-control,
.Formula-Designer .u-modal-content .u-modal-body .RightContent .ant-row:nth-of-type(2) .input-control {
  width: 380px !important;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .ant-row:nth-of-type(4),
.Formula-Designer .u-modal-content .u-modal-body .RightContent .ant-row:nth-of-type(4) {
  margin-bottom: 15px;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .formula-editing,
.Formula-Designer .u-modal-content .u-modal-body .RightContent .formula-editing {
  position: relative;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .formula-editing textarea.ant-input,
.Formula-Designer .u-modal-content .u-modal-body .RightContent .formula-editing textarea.u-input {
  height: 150px !important;
  position: relative;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .formula-editing.ant-row,
.Formula-Designer .u-modal-content .u-modal-body .RightContent .formula-editing.ant-row {
  margin-bottom: 20px;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .formula-editing .error,
.Formula-Designer .u-modal-content .u-modal-body .RightContent .formula-editing .error {
  position: absolute;
  bottom: -107px;
  font-size: 12px;
  color: #ee2223;
  left: 85px;
  width: 410px;
  height: 108px;
  overflow-y: auto;
  overflow-x: hidden;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .operator .ant-row .input-control,
.Formula-Designer .u-modal-content .u-modal-body .RightContent .operator .ant-row .input-control {
  width: 360px !important;
}
/*底部按钮*/
.Formula-Designer .ant-modal-content .ant-modal-footer,
.Formula-Designer .u-modal-content .u-modal-footer {
  height: 48px;
  padding: 10px 24px;
}
.Formula-Designer .ant-modal-content .ant-modal-footer .ant-btn,
.Formula-Designer .u-modal-content .u-modal-footer .u-button {
  padding: 0 12px;
  width: 60px;
}
.Formula-Designer .ant-modal-content .ant-modal-footer .ant-btn:nth-of-type(1),
.Formula-Designer .u-modal-content .u-modal-footer .u-button:nth-of-type(1) {
  background-color: #eceff1;
  border: none;
  color: #505766;
}
.Formula-Designer .ant-modal-content .ant-modal-footer .ant-btn:nth-of-type(1):hover,
.Formula-Designer .u-modal-content .u-modal-footer .u-button:nth-of-type(1):hover {
  background-color: #dfe1e6;
  border: none;
}
.Formula-Designer .ant-modal-content .ant-modal-footer .ant-btn:nth-of-type(2),
.Formula-Designer .u-modal-content .u-modal-footer .u-button:nth-of-type(2) {
  background: #505766;
  border: none;
  color: #fff;
}
.Formula-Designer .ant-modal-content .ant-modal-footer .ant-btn:nth-of-type(2):hover,
.Formula-Designer .u-modal-content .u-modal-footer .u-button:nth-of-type(2):hover {
  background: #616875;
  color: #fff;
}
.Formula-Designer .ant-modal-content .ant-modal-footer .ant-btn:nth-of-type(3),
.Formula-Designer .u-modal-content .u-modal-footer .u-button:nth-of-type(3) {
  background: linear-gradient(-45deg, #fc4c2f, #ee2223);
  color: #fff;
}
.Formula-Designer .ant-modal-content .ant-modal-footer .ant-btn:nth-of-type(3):hover,
.Formula-Designer .u-modal-content .u-modal-footer .u-button:nth-of-type(3):hover {
  background: linear-gradient(-45deg, #fc4c2f, #ee2223);
  opacity: 0.8;
  color: #fff;
}
.Formula-Designer .ant-modal-content .ant-modal-footer .ant-btn:nth-of-type(3)[disabled],
.Formula-Designer .u-modal-content .u-modal-footer .u-button:nth-of-type(3)[disabled] {
  background: linear-gradient(-45deg, #fc4c2f, #ee2223);
  opacity: 0.5;
  border: none;
}
.formula-designer-autocomplete-content {
  margin-top: -20px;
  padding-right: 10px;
}
.formula-designer-autocomplete-content .u-select .u-select-arrow:before {
  content: "\e518";
  font-family: iconfont!important;
  font-size: 18px;
  transform: translateY(-4px);
}
.formula-designer-autocomplete-content .u-select-open .u-select-arrow:before {
  transform: translateY(-4px);
}
.formula-designer-autocomplete-content .ant-select-auto-complete.ant-select .ant-select-selection,
.formula-designer-autocomplete-content .u-select-auto-complete.u-select .u-select-selection {
  border: 1px solid #d9d9d9;
  border-radius: 5px!important;
}
.formula-designer-autocomplete-content:hover .ant-select-auto-complete.ant-select .ant-select-selection,
.formula-designer-autocomplete-content:hover .u-select-auto-complete.u-select .u-select-selection {
  border: 1px solid #000;
}
.formula-designer-autocomplete-content .anticon-question-circle-o {
  position: absolute;
  width: 20px;
  height: 28px;
  line-height: 28px;
  cursor: pointer;
  color: #555;
}
.formula-designer-autocomplete-content .formula-designer-search {
  font-size: 14px;
}
.Formula-Designer .anticon-question-circle-o::before {
  content: "\e766" !important;
}
.formula-designer-autocomplete-content .ant-input,
.formula-designer-autocomplete-content .ant-input:hover,
.formula-designer-autocomplete-content .u-input,
.formula-designer-autocomplete-content .u-input:hover {
  border: none!important;
}
.Formula-Designer .LeftContent {
  padding-top: 38px;
}
.formula-designer-search-tooltip {
  z-index: 1700;
}
.LeftContent .formula-select-tree {
  overflow: auto;
  height: 440px;
}
.RightContent .show-more-operatorData {
  cursor: pointer;
  display: inline-block;
  min-width: 60px;
}
.RightContent .show-more-operatorData:hover {
  color: #999;
}
.Formula-Designer .ant-modal-content .ant-modal-body .RightContent .ant-row .input-control .ant-tag.formula-tags,
.u-modal .Formula-Designer .u-modal-content .u-modal-body .RightContent .ant-row .input-control .formula-tags {
  height: 28px;
  margin-right: 7px;
}
.Formula-Designer.ant-modal .ant-modal-content .ant-modal-body .RightContent .ant-row:nth-of-type(2) .input-control,
.u-modal .Formula-Designer .u-modal-content .u-modal-body .RightContent .ant-row:nth-of-type(2) .input-control {
  width: 345px !important;
}
.Formula-Designer.ant-modal .ant-modal-content .ant-modal-body .RightContent .ant-row:nth-of-type(1),
.u-modal .Formula-Designer .u-modal-content .u-modal-body .RightContent .ant-row:nth-of-type(1) {
  margin-bottom: 15px;
}
.Formula-Designer.ant-modal .ant-modal-content .ant-modal-body .RightContent .formula-editing textarea.ant-input,
.u-modal .Formula-Designer .u-modal-content .u-modal-body .RightContent .formula-editing textarea {
  height: 80px!important;
  resize: none;
}
.Formula-Designer .u-input-number.u-input-group.simple .u-form-control {
  height: 28px;
}
.formula-select-options.u-select-dropdown .u-select-dropdown-menu-item-active,
.formula-select-options.u-select-dropdown .u-select-dropdown-menu-item:hover {
  background: #fdf2dc;
}
