.transfer-table {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  overflow: hidden;
}
.transfer-table .to-be-select {
  -ms-flex: 1;
      flex: 1;
}
.transfer-table .has-select {
  -ms-flex: 1;
      flex: 1;
}
.transfer-table .transfer-btn {
  width: 60px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
}
.transfer-table .transfer-btn span {
  width: 28px;
  text-align: center;
  height: 34px;
  line-height: 34px;
  background: #EDF1F7;
  border-radius: 4px;
  color: #ccc;
  margin-bottom: 8px;
  cursor: pointer;
}
.transfer-table .transfer-btn span i {
  font-size: 12px;
}
.transfer-table .transfer-btn span.active i {
  color: #505766;
  font-weight: bold;
}
.transfer-table .transfer-btn span:hover {
  background: #E7E9F0;
}
.transfer-table .select-title {
  color: #EE2223;
  font-size: 14px;
  line-height: 28px;
  padding-left: 20px;
}
.transfer-table .select-title span {
  position: relative;
}
.transfer-table .select-title span::before {
  content: "";
  position: absolute;
  background-color: #505766;
  width: 0!important;
  height: 0;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-bottom: 4px solid #505766;
  background: none;
  left: 50%;
  bottom: -14px;
}
.transfer-table .select-search-box {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 8px;
}
.transfer-table .select-search-box .select-title {
  -ms-flex: 1;
      flex: 1;
}
.transfer-table .select-search-box > span {
  -ms-flex: 1;
      flex: 1;
  padding-right: 16px;
}
.transfer-table .select-out-box {
  width: 100%;
  border-radius: 4px;
}
.transfer-table .select-out-box .listTable {
  overflow-x: hidden;
  border-top-width: 0px;
}
.transfer-table .to-be-select .select-out-box .listTable {
  border-right: 1px solid #d9d9d9;
}
.transfer-table .has-select .select-out-box .listTable {
  border-left: 1px solid #d9d9d9;
}
.u-modal .transfer-table-container.no-left-tree {
  width: 1000px!important;
}
.u-modal .transfer-table-container.no-left-tree .u-modal-body {
  padding: 16px 0 0;
  overflow-y: hidden;
  height: 470px;
}
.u-modal .transfer-table-container.no-left-tree .u-modal-body .transfer-table {
  height: 454px;
}
.u-modal .transfer-table-container.no-left-tree .u-modal-body .transfer-table .listTable {
  height: 418px;
  overflow-y: hidden;
}
.u-modal .transfer-table-container.has-left-tree .u-modal-body {
  padding: 0px;
  overflow-y: hidden;
}
.u-modal .transfer-table-container.has-left-tree .u-modal-body > .ant-row {
  height: 100%;
  overflow-y: hidden;
}
.u-modal .transfer-table-container.has-left-tree .leftPanel {
  max-height: 100%!important;
  width: 200px;
  display: block;
  float: left;
  height: 470px;
}
.u-modal .transfer-table-container.has-left-tree .leftPanel .filter-tree > div:last-of-type {
  overflow-x: hidden!important;
}
.u-modal .transfer-table-container.has-left-tree .leftPanel .filter-tree .bg-white.border-r {
  border-right: 1px solid transparent!important;
}
.u-modal .transfer-table-container.has-left-tree .rightPanel {
  width: calc(100% - 200px) !important;
  border-left: #d9d9d9 solid 1px;
  display: block;
  float: left;
}
.u-modal .transfer-table-container.has-left-tree .rightPanel .scheme-list {
  margin-top: 8px;
}
