.msgcode {
  width: 300px;
  margin: 0 auto;
}
.msgcode-phonenum {
  line-height: 20px;
  margin: 25px auto 15px;
  font-size: 12px;
  color: #999;
  text-align: center;
}
.msgcode-info {
  padding: 0px 10px 40px 11px;
  overflow: hidden;
}
.msgcode-time {
  float: right;
  color: #999;
  height: 30px;
  line-height: 30px;
}
.msgcode-btn {
  color: #505766;
  background: #f5f5f5;
  float: right;
  line-height: 30px;
  padding: 0 12px;
  border-radius: 3px;
}
.msgcode-btn-disabled {
  color: inherit;
  display: none;
}
.msgcode-err {
  color: #EE2223;
  float: left;
  line-height: 12px;
  font-size: 12px;
}
.ant-modal .viewCell.width-percent-100 .msgcode-yxy {
  position: relative;
  height: 70px;
  overflow: hidden;
}
.ant-modal .viewCell.width-percent-100 .msgcode-yxy .ant-input {
  position: absolute;
  left: 0;
  z-index: 2;
  background-color: inherit;
  border: none;
  letter-spacing: 57px;
  font-size: 40px;
  line-height: 60px;
  height: 60px;
  padding-left: 28px;
  outline: none;
  text-indent: -999em;
  /*文本向左缩进*/
  margin-left: -100%;
  /*输入框光标起始点向左左移*/
  width: 200%!important;
  /*输入框增大一倍*/
  opacity: 0;
}
.ant-modal .viewCell.width-percent-100 .msgcode-yxy-box {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
}
.ant-modal .viewCell.width-percent-100 .msgcode-yxy-box p {
  border: #505766 solid 1px;
  width: 60px!important;
  height: 60px;
  color: #505766;
  font-size: 30px;
  text-align: center;
  margin: 0 10px;
  float: left;
  border-radius: 2px;
  line-height: 60px;
}
.ant-modal .viewCell.width-percent-100 .msgcode-yxy-box span {
  position: absolute;
  top: 12px;
  left: 54px;
  display: block;
  width: 1px;
  height: 35px;
  background-color: black;
  animation: blink 1s infinite steps(1, start);
}
@keyframes blink {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.msgcode-modal .ant-modal-header {
  border-bottom: none;
}
.msgcode-modal .modal-container {
  height: 180px;
}
