.uretail-notification {
  z-index: 1710!important;
}
.uretail-notification .anticon {
  position: absolute;
  left: 12px;
}
.uretail-notification .anticon-circle {
  font-size: 12px;
  color: #666;
  white-space: pre-wrap;
  margin-left: 4px;
}
.uretail-notification .uretail-notification-notice-content {
  max-width: 400px;
  word-break: break-all;
}
.uretail-notification .uretail-message-custom-content {
  position: relative;
  padding-left: 32px !important;
}
.uretail-notification .icon {
  font-size: 18px;
  text-align: center;
  margin-top: 2px;
}
.uretail-notification .uretail-message-success {
  background: #EBF8F2;
  border: #A7E1C4 solid 1px;
  border-radius: 4px;
  width: auto;
  line-height: 20px;
  color: #666;
  padding: 8px 12px;
}
.uretail-notification .uretail-message-success .icon {
  color: #18B681;
}
.uretail-notification .uretail-message-info {
  background: #E6F0F7;
  border: #ADD8F7 solid 1px;
  border-radius: 4px;
  width: auto;
  line-height: 20px;
  color: #666;
  font-size: 12px;
  padding: 8px 12px;
}
.uretail-notification .uretail-message-info .icon {
  color: #588CE9;
}
.uretail-notification .uretail-message-error {
  background: #FEF3EB;
  border: #FCCCA7 solid 1px;
  border-radius: 4px;
  width: auto;
  line-height: 20px;
  color: #666;
  font-size: 12px;
  padding: 8px 32px 8px 12px;
}
.uretail-notification .uretail-message-error .icon {
  color: #FF5735;
}
.uretail-notification .uretail-message-warning {
  background: #FFFAEB;
  border: #FFE9A7 solid 1px;
  border-radius: 4px;
  width: auto;
  line-height: 20px;
  color: #666;
  font-size: 12px;
  padding: 8px 12px;
}
.uretail-notification .uretail-message-warning .icon {
  color: #FFA600;
}
