.crossArea::after {
  content: '';
  display: block;
  clear: both;
}
.crossArea .fl {
  width: 300px;
  float: left;
  margin-right: 10px;
  height: 400px;
  border: 1px solid #E9E9E9;
}
.crossArea .fr {
  width: 500px;
  float: left;
  max-width: 10000px;
}
.crossArea .fl .DraggableTags-tag {
  display: block;
}
.crossArea .fc {
  float: left;
}
.crossArea .fc > div {
  width: 100px;
}
.crossArea .drag-checked {
  border: 1px dashed #94D0FF;
  background: #f2f6ff;
  min-height: 42px;
  min-width: 165px;
}
.crossArea .drag-checked.nodata {
  display: none;
}
.DraggableTags {
  min-height: 54px;
  width: 100%;
  /* overflow: hidden; */
  display: -ms-flexbox;
  display: flex;
  padding-right: 80px;
}
.DraggableTags .DraggableTags-tag {
  margin-right: 25px;
  display: block;
  float: left;
  max-width: 500px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  word-break: keep-all;
}
.DraggableTags-tag-drag {
  position: relative;
}
.DraggableTags .DraggableTags-tag:nth-of-type(1) {
  min-width: auto;
  max-width: 500px;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.DraggableTags .DraggableTags-tag-drag button {
  left: -25px;
  top: 7px;
  position: absolute;
}
.crossArea .tag {
  font-size: 12px;
  border: 1px solid #e6e6f7;
  border-radius: 3px;
  padding: 0 10px;
  line-height: 26px;
  color: #666666;
  background: #fff;
  position: relative;
}
.crossArea .tag .close-button {
  display: none;
  position: absolute;
  right: -6px;
  top: 7px;
  width: 12px;
  height: 12px;
}
.crossArea .tag:hover {
  border: #505766 solid 1px;
}
.crossArea .tag:hover .close-button {
  display: block;
}
.crossArea .right .tag-checked {
  border-style: solid;
}
.crossArea .right .tag span {
  width: 15px;
  height: 15px;
  display: inline-block;
  text-align: center;
  line-height: 12px;
  position: absolute;
  left: -21px;
  top: 4px;
  font-weight: bold;
  font-size: 20px;
  color: #505766;
}
.crossArea .right .delete {
  position: absolute;
  top: -1px;
  right: -1px;
  width: 16px;
  height: 16px;
  cursor: pointer;
  user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
.crossArea .fr {
  border: 1px solid #E9E9E9;
  padding: 10px;
  height: 400px;
  overflow: auto;
}
.crossArea-list {
  position: relative;
}
.crossArea-list-content {
  min-width: 165px;
  position: absolute;
  height: 36px;
}
.crossArea-list-content:nth-last-child(1) {
  height: 80px;
}
.crossArea .square {
  border-radius: 4px;
  padding: 5px;
  height: 100%;
}
.crossArea-list-content::before {
  content: '';
  display: block;
  width: 46px;
  height: 46px;
  border-left: solid 1px #D5D5D5;
  border-bottom: solid 1px #D5D5D5;
  position: absolute;
  left: -41px;
  top: -10px;
}
.crossArea-list-content:nth-last-child(1)::before {
  border: none;
}
.crossArea-list-content:first-child::before {
  border-left: none;
}
.crossArea-list-content > button {
  position: absolute;
  left: -47px;
  top: 28px;
  width: 14px;
  height: 14px;
  line-height: 14px;
  padding: 0px;
  border-radius: 2px;
  z-index: 99;
  border: #ccc solid 1px;
  min-width: 0;
}
.crossArea-list-content > button .anticon-tianjiashuliang:before,
.crossArea-list-content > button .anticon-jianshaoshuliang:before {
  font-size: 12px;
  text-align: center;
  line-height: 12px;
  font-weight: bold;
  transform: scale(0.7);
  margin-top: -2.6px;
  margin-left: 3px;
  color: #000;
}
.crossArea-list-content:last-child {
  height: 54px;
}
.DraggableTags-tag-drag > div:after {
  content: '';
  display: block;
}
.DraggableTags-tag-drag .tag {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.crossArea .tag .anticon-close {
  margin-left: 5px;
}
.crossArea .tag .anticon-close:before {
  color: #C4C5C7;
  font-size: 12px;
  transform: scale(0.7);
}
.crossArea .tag .anticon-close:hover:before {
  color: #ee2233;
}
