.keyboard {
  width: 100%;
  max-width: 1030px;
  margin: 0 auto;
  background: #dadada;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.5);
}
.keyboard-wrapper {
  position: fixed;
  bottom: 0;
  z-index: 10000;
  width: 700px;
  left: calc(50% - 379px);
}
.keyboard-wrapper .keyboard-row {
  display: -ms-flexbox;
  display: flex;
}
.keyboard-button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-preferred-size: 100px;
      flex-basis: 100px;
  font-size: 18px;
  height: 60px;
  border-radius: 4px;
  background-color: #F5F5F5;
  border: 1px solid #CECECE;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
}
.keyboard-button:focus {
  outline: none;
}
.keyboard-button:disabled {
  opacity: 0.4;
  cursor: default;
}
.keyboard-button:disabled {
  opacity: 0.4;
  cursor: default;
}
.keyboard-button:active {
  background-color: #cccccc;
}
.keyboard-button.shift-symbols {
  -ms-flex-preferred-size: 210px;
      flex-basis: 210px;
}
.keyboard-button.keyboard-numberButton {
  -ms-flex-positive: 1;
      flex-grow: 1;
}
.keyboard-button.keyboard-halfButton {
  -ms-flex-preferred-size: 56px;
      flex-basis: 56px;
}
.keyboard-button.keyboard-space {
  -ms-flex-positive: 1;
      flex-grow: 1;
}
.keyboard-button.keyboard-utilButton {
  -ms-flex-positive: 2;
      flex-grow: 2;
  max-width: 55px;
}
.keyboard-button.keyboard-additionalButton {
  -ms-flex-preferred-size: 128px;
      flex-basis: 128px;
}
.keyboard-button.keyboard-submitButton,
.keyboard-button.keyboard-submit-button {
  -ms-flex-preferred-size: 95px;
      flex-basis: 95px;
  background-color: #63b324;
  border-color: #63b324;
  color: #ffffff;
}
.keyboard-keysSet {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 1;
      flex-grow: 1;
}
