.mdf-autocomplete-input {
  height: 28px;
}
.mdf-autocomplete-input .u-autocomplete {
  width: 100%;
}
.mdf-autocomplete-input .u-autocomplete .u-form-control {
  height: 28px;
}
.mdf-autocomplete-input .u-autocomplete-options {
  max-height: 300px;
  overflow-y: scroll;
  background: white;
}
.mdf-autocomplete-input .u-select .u-select-selection-clear {
  right: 5px;
}
.mdf-autocomplete-input .u-select-search-field {
  padding-right: 18px;
}
.has-error .u-autocomplete .u-form-control {
  border-color: #f44336;
}
.u-select-dropdown .mdf-autocomplete.u-select-dropdown-menu-item {
  -webkit-line-clamp: 1;
  color: #333;
  padding: 6px 20px;
  padding-left: 20px;
  display: -ms-flexbox;
  display: flex;
}
.u-select-dropdown.mdf-autocomplete-drop .u-select-dropdown-menu {
  max-height: 250px;
  padding: 8px 0;
}
.u-select-dropdown.mdf-autocomplete-drop .u-select-dropdown-menu .u-select-dropdown-menu-item span {
  display: inline-block;
  line-height: 22px;
  overflow: hidden;
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
}
