.inputemail {
  width: 100%;
  height: 100%;
  display: inline-block;
}
.inputemail > div {
  width: 100%;
}
.inputemail .inputemail-control {
  line-height: 0px;
}
.inputemail .inputemail-control .u-form-control {
  width: calc(100% - 120px);
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  vertical-align: top;
}
.inputemail .inputemail-select {
  height: 100%;
  width: 120px;
  display: inline-block;
}
.inputemail .inputemail-select .u-select .u-select-selection {
  border-left: 0px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
}
.inputemail .inputemail-select .u-select .u-select-selection:hover {
  border: 1px solid #d9d9d9;
  border-left: 0px;
}
.inputemail .inputemail-select .u-select-focused .u-select-selection,
.inputemail .inputemail-select .u-select-selection:active,
.inputemail .inputemail-select .u-select-selection:focus {
  border: 1px solid #d9d9d9 !important;
  border-left: 0px  !important;
}
