/*公式编辑器*/
.FormulaDesigner {
  height: 586px;
  background: #fff;
  box-shadow: 0 0 14px 6px #666666;
  border-radius: 6px;
}
.FormulaDesigner .ant-input-number {
  height: 30px;
}
.FormulaDesigner .ant-input-number-input {
  height: 28px;
}
.FormulaDesigner .ant-select-selection--single {
  height: 28px !important;
}
.FormulaDesigner .ant-modal-content {
  /*头部*/
  /*内容*/
  /*底部按钮*/
}
.FormulaDesigner .ant-modal-content .ant-modal-body {
  height: 494px;
  padding: 0px 0px 0px 20px;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .LeftContent {
  width: 232px;
  float: left;
  height: 494px;
  overflow-y: auto;
  border-right: #E4E4E4 solid 1px;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent {
  width: 514px;
  float: left;
  margin-left: 34px;
  padding-top: 20px;
  height: 494px;
  overflow-y: auto;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .add_defined .refer-div.has-feedback .ant-row {
  margin-bottom: 0;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .ant-row {
  margin-bottom: 15px;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .ant-row .label-control {
  width: 68px !important;
  color: #2A2A2A;
  text-align: right;
  line-height: 28px;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .ant-row .input-control {
  margin-left: 15px;
  width: 350px !important;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .ant-row .input-control .ant-select {
  float: left;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .ant-row .input-control .ant-tag {
  min-width: 50px;
  height: 30px;
  background: #fff;
  line-height: 26px;
  text-align: center;
  margin-bottom: 10px;
  margin-left: 0px;
  margin-right: 10px;
  border: 1px solid #D9D9D9;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .ant-row .input-control .ant-tag .ant-tag-text {
  color: #666;
  font-size: 15px;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .ant-row .input-control .ant-tag:hover {
  border: 1px solid #505766;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .ant-row:nth-of-type(1) {
  margin-bottom: 20px;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .ant-row:nth-of-type(2) {
  margin-bottom: 15px;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .ant-row:nth-of-type(2) .input-control {
  width: 380px !important;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .ant-row:nth-of-type(4) {
  margin-bottom: 15px;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .formula-editing {
  position: relative;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .formula-editing textarea.ant-input {
  height: 150px !important;
  position: relative;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .formula-editing.ant-row {
  margin-bottom: 20px;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .formula-editing .error {
  position: absolute;
  bottom: -107px;
  font-size: 12px;
  color: #ee2223;
  left: 85px;
  width: 410px;
  height: 108px;
  overflow-y: auto;
  overflow-x: hidden;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .operator .ant-row .input-control {
  width: 360px !important;
}
.FormulaDesigner .ant-modal-content .ant-modal-footer {
  height: 48px;
  padding: 10px 24px;
}
.FormulaDesigner .ant-modal-content .ant-modal-footer .ant-btn {
  padding: 0 12px;
  width: 60px;
}
.FormulaDesigner .ant-modal-content .ant-modal-footer .ant-btn:nth-of-type(1) {
  background-color: #eceff1;
  border: none;
  color: #505766;
}
.FormulaDesigner .ant-modal-content .ant-modal-footer .ant-btn:nth-of-type(1):hover {
  background-color: #dfe1e6;
  border: none;
}
.FormulaDesigner .ant-modal-content .ant-modal-footer .ant-btn:nth-of-type(2) {
  background: #505766;
  border: none;
  color: #fff;
}
.FormulaDesigner .ant-modal-content .ant-modal-footer .ant-btn:nth-of-type(2):hover {
  background: #616875;
}
.FormulaDesigner .ant-modal-content .ant-modal-footer .ant-btn:nth-of-type(3) {
  background: linear-gradient(-45deg, #fc4c2f, #ee2223);
  color: #fff;
}
.FormulaDesigner .ant-modal-content .ant-modal-footer .ant-btn:nth-of-type(3)[disabled] {
  background: linear-gradient(-45deg, #fc4c2f, #ee2223);
  opacity: 0.5;
  border: none;
}
/*新增订阅方案*/
.FormulaDesigner .subscribe_cycle_radio .subscribe_cycle_select {
  width: 185px;
  margin-left: 10px;
  float: left;
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.FormulaDesigner .subscribe_cycle_radio .subscribe_cycle_select .ant-select {
  width: 80px !important;
  margin: 0 10px;
}
.FormulaDesigner .RightContent .subscribe_condition_inputArea,
.FormulaDesigner .RightContent textarea.error_info {
  height: 63px;
  resize: none;
}
.FormulaDesigner .RightContent .error_info {
  border: #ee2223 solid 1px;
}
.FormulaDesigner .RightContent .select_error_info > .ant-select-selection--multiple {
  border: #ee2223 solid 1px;
}
.FormulaDesigner .RightContent .frequency_error_info > .ant-select-selection--multiple {
  border: #ee2223 solid 1px;
}
.FormulaDesigner .subscribe_cycle_week_or_month,
.FormulaDesigner .frequency_error_info {
  margin-top: 15px;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .ant-row .input-control .ant-calendar-picker-input input {
  text-align: left;
}
.FormulaDesigner .ant-modal-content .ant-modal-body .RightContent .ant-row .input-control .ant-calendar-picker-input input + span + input {
  margin-left: 20px;
}
.FormulaDesigner .subscribe_condition {
  position: relative;
  margin-bottom: 20px;
}
.FormulaDesigner .subscribe_condition .error {
  position: absolute;
  color: #EE2223;
  left: 83px;
  bottom: -18px;
}
.FormulaDesigner .receiver_people {
  margin-bottom: 20px;
  position: relative;
}
.FormulaDesigner .receiver_people .ant-select .ant-select-selection {
  overflow-y: auto;
  max-height: 84px;
}
.FormulaDesigner .receiver_people .error {
  position: absolute;
  color: #EE2223;
  left: 83px;
  bottom: -18px;
}
.FormulaDesigner .receiver_people:first-child {
  margin-bottom: 10px;
}
.FormulaDesigner .receiver_people:first-child .ant-row {
  margin-bottom: 0px !important;
}
.FormulaDesigner .operator .ant-row {
  margin-bottom: 10px !important;
}
.FormulaDesigner .receiver_people:last-child {
  margin-bottom: 60px;
}
