.product-price li {
  float: left;
  width: 50%;
}
.product-price li p {
  margin-bottom: 7px;
}
.product-price li span {
  display: inline-block;
  color: #999;
}
.product-price li em {
  font-style: normal;
  color: #333;
}
.product-price li strong {
  color: #333;
  line-height: 42px;
  font-weight: normal;
}
.product-state li span {
  color: #333;
}
.product-state li.lower-frame strong {
  color: #588CE9;
}
