.ac_mobile_locale .mobile-locale {
  height: 28px;
  line-height: 28px;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}
.ac_mobile_locale .mobile-locale .u-form-control {
  height: 24px;
  border: 0px;
}
.ac_mobile_locale .mobile-locale .country-code {
  height: 27px;
  line-height: 27px;
}
.ac_mobile_locale .location {
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}
.ac_mobile_locale .location .u-select {
  margin-top: -2px;
}
.ac_mobile_locale .location .u-select .u-select-selection {
  background: transparent;
}
.ac_mobile_locale .location .u-select .u-select-selection .u-select-selection-rendered {
  line-height: 30px !important;
}
.ac_mobile_locale .location .u-select .u-select-selection .u-select-arrow {
  top: 55% !important;
  right: 5px !important;
  font-size: 16px;
}
.ac_mobile_locale .location .u-select .u-select-selection:focus {
  border: 0px !important;
}
.ac_mobile_locale .input-phone-read {
  width: auto;
  height: 28px;
  display: -ms-flexbox;
  display: flex;
  border: 1px solid #d9d9d9;
  border-radius: 3px;
}
.ac_mobile_locale .input-phone-read > li {
  padding: 0 10px;
  border-right: 1px solid #d9d9d9;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
}
.ac_mobile_locale .input-phone-read > li:last-child {
  border: 0px;
}
