.pictureupload .pictureupload-upload-action {
  display: inline-block;
  vertical-align: top;
}
.pictureupload .pictureupload-upload-action-but {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.pictureupload .pictureupload-imagecard {
  display: inline-block;
}
.pictureupload .pictureupload-imagecard-item {
  border-radius: 4px;
  border: 1px solid #d9d9d9;
  position: relative;
  display: inline-block;
  width: 96px;
  height: 96px;
  margin: 0px 8px 0px 0px;
  transition: all 0.3s ease;
}
.pictureupload .pictureupload-imagecard-item:hover:before {
  opacity: 0.8;
}
.pictureupload .pictureupload-imagecard-item:hover .pictureupload-imagecard-item-toolbar {
  opacity: 1;
  color: #fff;
}
.pictureupload .pictureupload-imagecard-item:hover .pictureupload-imagecard-item-dowload {
  height: 32px;
}
.pictureupload .pictureupload-imagecard-item:before {
  content: " ";
  position: absolute;
  z-index: 1;
  background-color: gray;
  transition: all 0.3s ease;
  opacity: 0;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
}
.pictureupload .pictureupload-imagecard-item img {
  padding: 8px;
  width: 48px;
  height: 48px;
  overflow: hidden;
  border-radius: 2px;
  display: block;
  width: 100%;
  height: 100%;
  position: static;
}
.pictureupload .pictureupload-imagecard-item-toolbar {
  text-align: center;
  position: absolute;
  top: 50%;
  margin-top: -16px;
  width: 100%;
  color: #d2d2d2;
  z-index: 2;
  opacity: 0;
}
.pictureupload .pictureupload-imagecard-item-toolbar .iconfont {
  cursor: pointer;
}
.pictureupload .pictureupload-imagecard-item-toolbar .icon-delete {
  margin-left: 5px;
}
.pictureupload .pictureupload-imagecard-item-dowload {
  text-align: center;
  position: absolute;
  width: 100%;
  bottom: 0px;
  background: #4d4c4c;
  color: #d2d2d2;
  z-index: 2;
  opacity: 0.7;
  height: 0px;
  overflow: hidden;
}
.pictureupload .pictureupload-imagecard-item-dowload a {
  color: #d2d2d2;
}
.pictureupload .pictureupload-imagecard-item-dowload a:hover {
  color: #fff;
}
.pictureupload .pictureupload-imagecard-item-dowload a.active {
  color: #fff;
}
.pictureupload-err-text {
  top: auto !important;
}
