.ac-input-locale .u-autocomplete {
  width: 100%;
}
.ac-input-locale .u-autocomplete-options {
  max-height: 300px;
  overflow-y: scroll;
  background: white;
}
.ac-input-locale .input-text.u-autocomplete {
  padding-right: 0px;
  width: 100%;
}
.ac-input-locale .input-text.u-autocomplete input {
  padding-right: 30px;
}
.ac-input-locale .u-select.input-text {
  padding-right: 0px;
}
.ac-input-locale .u-select.input-text .u-select-search-field {
  padding-right: 26px;
}
.u-select-dropdown .mdf-autocomplete.u-select-dropdown-menu-item {
  -webkit-line-clamp: 1;
  color: #333;
  padding: 6px 20px;
  padding-left: 20px;
  display: -ms-flexbox;
  display: flex;
}
.u-select-dropdown.mdf-autocomplete-drop .u-select-dropdown-menu {
  max-height: 250px;
  padding: 8px 0;
}
.u-select-dropdown.mdf-autocomplete-drop .u-select-dropdown-menu .u-select-dropdown-menu-item span {
  display: inline-block;
  line-height: 22px;
  overflow: hidden;
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
}
